export * from "./v0";
export function upgrade(oldNotebook) {
    var newNotebook = oldNotebook;
    newNotebook.models.forEach(function (model) {
        model.tapes = 1;
        model.blank = oldNotebook.blank;
        model.wildcard = oldNotebook.wildcard;
        model.transitions.forEach(function (t) {
            t.read = [t.read];
            t.write = [t.write];
            t.direction = [t.direction];
        });
    });
    newNotebook.version = 1;
    return newNotebook;
}
