import Vue from 'vue';
import _ from "lodash";
import IconBtn from "@/components/IconBtn.vue";
import Tape from "@/shared/Tape";
import run from "@/store/run";
import * as d3 from "d3";
import global from "@/store/global";
import simulator, { Event } from '@/shared/simulator';
import { Direction } from '@/shared/types';
export default Vue.extend({
    components: { IconBtn: IconBtn },
    data: function () {
        return {
            global: global,
            run: run,
            tape: null,
            transition: null,
            resetTape: null,
        };
    },
    watch: {
        model: function (newModel, oldModel) {
            if (newModel !== oldModel)
                this.setupTapes();
        }
    },
    computed: {
        model: function () { return global.model; },
        tapes: function () { return global.model.tapes; },
        status: function () { return run.status; },
        playing: function () { return run.playing; },
        stepThrottle: function () {
            return _.throttle(function () { return run.step(); }, simulator.interval, { trailing: false });
        },
    },
    methods: {
        play: function () { return run.play(); },
        pause: function () { return run.pause(); },
        back: function () { return run.back(); },
        reset: function () { return run.reset(); },
        step: function () { return run.step(); },
        setupTapes: function () {
            var _this = this;
            // Refresh model and default tape
            simulator.setModel(global.model);
            simulator.load(Array(global.model.tapes).fill(""));
            simulator.reset();
            var tapeWrapper = d3.select(this.$refs.tapeWrapper);
            tapeWrapper.html(null);
            this.tape = _.times(this.tapes, function (i) {
                var tapeEl = document.createElementNS("http://www.w3.org/2000/svg", "svg");
                tapeEl.setAttribute("class", "tape");
                (_this.$refs.tapeWrapper).appendChild(tapeEl);
                var tape = new Tape(tapeEl, function () { return simulator.turing.snapshot.head[i]; }, function () { return simulator.turing.snapshot.tape[i]; });
                return tape;
            });
        }
    },
    mounted: function () {
        var _this = this;
        this.setupTapes();
        // Sync when resetting
        this.resetTape = function () {
            _this.tape.forEach(function (x) { return x.reset(); });
        };
        simulator.bus.$on([Event.BACK, Event.RESET], this.resetTape);
        // Animate transition
        this.transition = function (t) {
            _this.tape.forEach(function (x, i) {
                return x.transition({
                    read: t.read[i],
                    write: t.write[i],
                    direction: [Direction.Left, Direction.Stay, Direction.Right][t.direction[i] + 1]
                });
            });
        };
        simulator.bus.$on(Event.TRANSITION, this.transition);
        window.addEventListener("resize", this.resetTape);
    },
    destroyed: function () {
        // Once we close our tape, we can halt the simulator
        run.pause();
        // Stop listening for simulator events
        simulator.bus.$off([Event.UPDATE, Event.BACK], this.resetTape);
        simulator.bus.$off(Event.TRANSITION, this.transition);
        // Stop listening for window resizes (we no longer need to resize our tape after it's destroyed)
        window.removeEventListener("resize", this.resetTape);
    }
});
