import Vue from 'vue';
import _ from "lodash";
import Section from "@/components/SideBar/Section.vue";
import Button from "@/components/Button.vue";
import Input from "@/components/Input.vue";
import global from "@/store/global";
import run from "@/store/run";
export default Vue.extend({
    data: function () {
        return {
            input: [],
            global: global,
            run: run,
        };
    },
    watch: {
        tapes: {
            immediate: true,
            handler: function (newTapes) {
                var _this = this;
                this.input = _.times(newTapes, function (i) { return _this.input[i] || ""; });
            }
        }
    },
    computed: {
        tapes: function () { return global.model.tapes; }
    },
    methods: {
        load: function () {
            run.load(_.cloneDeep(this.input));
        }
    },
    components: { Section: Section, Button: Button, Input: Input }
});
